.comparison-table {
    width: 100%; /* 表の幅を100%に設定 */
    margin: 20px auto; /* 上下のマージンを追加し、左右は自動で中央寄せ */
    text-align: center; /* テキストを中央揃え */
  }
  
  .comparison-table h2 {
    margin-bottom: 20px; /* ヘッダーと表の間の余白 */
  }
  
  .comparison-table table {
    margin: 0 auto; /* テーブルも中央に寄せる */
    border-collapse: collapse; /* セルの境界線を一本線にする */
    max-width: 800px; /* テーブルの最大幅を設定 */
  }
  
  /* テーブルヘッダーのスタイル */
  .comparison-table th {
    background-color: #d9ecff; /* 背景色 */
    color: #333; /* 文字色 */
    padding: 10px; /* パディング */
    border: 1px solid #ddd; /* ボーダー */
  }
  
  /* テーブルデータのスタイル */
  .comparison-table td {
    text-align: center; /* セルの内容も中央揃え */
    padding: 10px; /* パディング */
    border: 1px solid #ddd; /* ボーダー */
  }
  
  /* リンクのスタイル */
  .comparison-table td a {
    color: #0066cc; /* リンクの色 */
    text-decoration: none; /* 下線を無効化 */
  }
  
  .comparison-table td a:hover {
    text-decoration: underline; /* ホバー時に下線を表示 */
  }
  .sidebar {
    /* 既存のスタイル */
  }
  
  @media (max-width: 768px) {
    .sidebar {
      /* スマートフォン用のサイドバースタイル */
    }
  }
  
